.search-box {
  max-width: 30px;
  width: 100%;
  height: 31px;
  transition: all .5s;
  position: relative;
  overflow: hidden;
}

.search-box.active {
  max-width: 173px;
  border: 1px solid #111321;
  border-radius: 20px;
}

.search-box__icon-svg {
  width: 15px;
  height: 15px;
  cursor: pointer;
  z-index: 999;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  left: 14px;
}

input {
  width: 100%;
  height: 100%;
  color: #111321;
  letter-spacing: .02em;
  border: none;
  outline: none;
  padding: 0 35px 0 48px;
  font-size: 12px;
  position: absolute;
  top: 0;
}

.search-box__icon {
  height: 100%;
  justify-content: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.close-icon {
  width: 15px;
  height: 15px;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  right: 15px;
  overflow: hidden;
  transform: translateY(-50%);
}

.close-icon:before {
  content: "";
  width: .5px;
  height: 10px;
  background: #999;
  position: absolute;
  transform: rotate(45deg);
}

.close-icon:after {
  content: "";
  width: .5px;
  height: 10px;
  background: #999;
  position: absolute;
  transform: rotate(-45deg);
}

